import React, { Component } from 'react';
import { Button, Row, Col, Tooltip, OverlayTrigger } from 'react-bootstrap';
import { connect } from "react-redux";

const CurrentOverView = ({ heading, value, btnText, history, toggleInvoice, icon, viewInvoice }) => {
    return <Col xl={3} sm={6}  className="py-2 px-3 d-flex align-items-center border-end my-sm-2 border-bottom">
            <div className='d-flex flex-grow-1 align-items-center pe-2'>
                <div className="symbol symbol-44 flex-shrink-0 me-3 bg-light text-gray-900">
                    <i className={`${icon} fw-normal icon-3 `}></i>
                </div>
                <div className="overflow-hidden text-start">
                    <h6 className="fs-7 fw-medium mb-1 text-gray-dark text-ellipsis">{heading}</h6>
                    <p className="fs-8 mb-0 text-gray-600 text-ellipsis block-center justify-content-start">{value}
                    {viewInvoice ? <OverlayTrigger placement="top" overlay={<Tooltip>Download last Invoice</Tooltip>}>
                        <button type="button" className="btn btn-icon h-auto w-auto p-0 border-0 ms-2" onClick={()=>viewInvoice()}><i className="ri-download-2-line icon-5"></i></button>
                    </OverlayTrigger> : null}
                    </p>
                </div>
            </div>
            {btnText ? <Button variant='primary' size={'sm'} className='text-nowrap' onClick={()=>{btnText.includes("Plan") ? history('/billing/price') : toggleInvoice()}}>{btnText}</Button> : null}
        </Col>
}

class BillingOverview extends Component {
    state = {
        expiresAt: '',
        plan: "UGC Suite",
        paymentMethod:"",
        billCycle:''
    }
    componentDidMount() {
        const { userData } = this.props
       // const { expiresAt } = this.state

        if (userData && Object.keys(userData.UserRule).length > 0) {
            this.setState({billCycle:userData.UserRule.billingCycle, expiresAt:userData.UserRule.billingDate, paymentMethod: userData.UserRule.paymentMode, plan: `${userData.UserPlan.Plan.name}`})
        }
    }
    render() {
        const { expiresAt, plan, paymentMethod, billCycle } = this.state
        const { showInvoice, history, toggleInvoice, viewInvoice } = this.props

        return <>
                <h3 className='fs-7'>Plan Details</h3>
                <div className="p-3 py-0 py-lg-3 p-0 mb-8 border rounded-2">
                    <Row className='plan_details'>
                        <CurrentOverView heading="Plan Name" value={plan} btnText="Change Plan" history={history} icon='ri-sparkling-2-line' />
                        <CurrentOverView heading="Billing Cycle" value={billCycle} icon='ri-exchange-dollar-line'/>
                        <CurrentOverView heading="Payment Method" value={paymentMethod} icon='ri-bank-card-2-line'/>
                        <CurrentOverView heading={"Invoice"} value={expiresAt} btnText={showInvoice ? "Show Usage" : "Show All Invoices"} toggleInvoice={toggleInvoice} icon={'ri-bill-line'} viewInvoice={viewInvoice} />
                    </Row>
                </div>
                </>
    }
}

const mapStateToProps = (state) => {
    return {
        userData: state.authenticated.userData
    }
}

export default connect(mapStateToProps)(BillingOverview);